<template>
    <div id="content">
        <audio id="myAudio" :src="audioUrl" muted loop></audio>
        <video id="bg-video">
            <source :src="videoUrl" type="video/mp4">
            <!-- <source src="./your-video.webm" type="video/webm"> -->
            Your browser does not support the video tag.
        </video>
        <div id="lead">
            <div id="cancel" @click="cancel">返回</div>
            <div id="text">{{ text }}</div>
        </div>
        <button id="play-button" @click="play">点击烧香</button>
    </div>
</template>
<script>
export default ({
    data() {
        return {
            text: '',
            videoUrl: require('../assets/video/不小心睡着.mp4'),
            audioUrl: require('../assets/audio/不小心睡着.mp3')
        }
    },
    mounted() {
        this.text = this.$route.params.tag
        if (this.text === '不小心睡着') {
            this.videoUrl = require('../assets/video/不小心睡着.mp4')
            this.audioUrl = require('../assets/audio/不小心睡着.mp3')
        } else if (this.text === '沉浸式神游') {
            this.videoUrl = require('../assets/video/沉浸式神游.mp4')
            this.audioUrl = require('../assets/audio/沉浸式神游.mp3')
        } else if (this.text === '短时放空') {
            this.videoUrl = require('../assets/video/短时放空.mp4')
            this.audioUrl = require('../assets/audio/短时放空.mp3')
        } else if (this.text === '轻微溜号') {
            this.videoUrl = require('../assets/video/轻微溜号.mp4')
            this.audioUrl = require('../assets/audio/轻微溜号.mp3')
        }

    },
    methods: {
        play() {
            document.getElementById('bg-video').play()
            document.getElementById('myAudio').play()
        },
        cancel(){
            this.$router.push({name:'tags'})
        }
    }
})
</script>
<style>
@font-face {
    font-family: "pingfang";
    src: url("@/assets/font/PingFang.ttc") format("truetype");
  }
#content {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 700px;
    width: 100%;
    box-sizing: border-box;
    cursor: url('../assets/image/cursor.ico'), pointer !important;
}

#bg-video {
    position: fixed;
    top: 0;
    left: 0;
    /* width: 100%;
    height: 100%; */
    object-fit: cover;
    z-index: -1;
}

#bg-video::-webkit-media-controls {
    display: none !important;
}

#play-button {
    align-self: flex-end;
    font-size: 30px;
    color: aquamarine;
    text-align: center;
    line-height: 50px;
    background: none;
    border: none;
    cursor: pointer;
    text-decoration: underline;
    text-decoration: none;
    position: absolute;
    bottom: 20px;
}

#lead {
    width: 100%;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: "pingfang",
}

#cancel,
#text {
    width: 200px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    color: gold;
    font-size: 30px;
}
</style>